<template lang="pug">
    b-form-checkbox-group(
      :id="kind"
      :name="kind"
      @input="$emit('input', data)"
      v-model="data")
      div(
        :key="kind+opt"
        v-for="opt in options"
      )
        transition(
          name="slide-fade"
          mode="out-in"
        )
          b-form-checkbox(
              v-if="!appearOnHover || (hovering || (data && data.length && data.indexOf(opt) !== -1))"
              :value="opt"
            ) {{ $t('tags.'+kind+'.'+opt )}}
</template>

<script>
  import {tagsOptions} from "@/config";

  export default {
    name: "TagCheckBoxGroup",
    components: {},
    props: ['kind', 'value', 'appearOnHover', 'hovering'],
    data() {
      return {
        // UX
        options: tagsOptions[this.kind],
        data: this.value
      };
    },
    methods: {
    },
  };
</script>
